<template>
    <div id="index1">
      <van-notice-bar mode="closeable">
        足协杯战线连续第2年上演广州德比战，上赛季半决赛上恒大以两回合5-3的总比分淘汰富力。
      </van-notice-bar>


      <router-view></router-view>
      <van-tabbar v-model="active" style="background-color: lightgrey">
        <van-tabbar-item icon="chat" to="/chat"></van-tabbar-item>
        <van-tabbar-item icon="home" dot to="/home"></van-tabbar-item>
        <van-tabbar-item icon="contact" info="20" to="/profile"></van-tabbar-item>
      </van-tabbar>

    </div>
</template>

<script>
  import router from "../router/index";
    export default {
      name: "index1",
      data() {
        return {
          active: 1
        }
      },
      methods:{
      },
      Mounted:function () {
        //防止刷新路由 和 图表不匹配
        var r = router.currentRoute.fullPath
        if (r=='/'){
          this.active = 0
        }else if(r=='/home'){
          this.active = 1
        }else if(r=='/profile'){
          this.active = 2
        }
      },
    }
</script>

<style scoped>

</style>



